<template>
  <div id="app">
    <div class="container">
      <div id="game-box" class="row">
        <div class="text-center main-cols">
          <img class="logo-sm" src="../assets/peoples-trivia-logo-med-white.png" />
          <p></p>
          <h3>Game Id: {{ gameid }}</h3>
          <button v-on:click="this.newGame" id="newgame" style="margin-right:5px;" class="btn btn-primary">Start New Game</button> <button v-on:click="this.getList" style="margin-left:5px;" id="computer" class="btn btn-success">Compute Scores</button>
          <p></p>
          <h4>Winner: {{ winner.team_name }} with a guess of {{ winner.guess }}</h4>
          <p></p>
          <table class="table">
            <tr>
              <th>Team Name</th>
              <th>Guess</th>
            </tr>
            <tr v-for="guess in list" :key="guess.team_name">
              <td>{{guess.team_name}}</td>
              <td>{{guess.guess}}</td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "People's Trivia Co.",
  computed: mapState({
    gameid: "gameid",
    list: "list",
    winner: "winner"
  }),
  data() {
    return {
      teamname: "",
      guess: "",
      submitted: false
    };
  },
  methods: {
    newGame: function(event) {
      event.preventDefault();
    },
    getList: function(event) {
      event.preventDefault();
      this.$store.commit("getList");
    }
  }
};
</script>

