import Vuex from 'vuex'
import Vue from 'vue'
import Axios from 'axios'
Vue.use(Vuex)

export const store = new Vuex.Store({
    state: {
        gameid: 1,
        list: [{
            "team_name": "cnc",
            "guess": 100
        },
        {
            "team_name": "cnc",
            "guess": 100
        },
        {
            "team_name": "cnc",
            "guess": 100
        },
        {
            "team_name": "cnc",
            "guess": 100
        }],
        winner: {
            "team_name": "cnc",
            "guess": 100
        }
    },
    mutations: {
        setGame(state, id) {
            state.gameid += id
        },
        setList(state, list) {
            state.list = list
        },
        setWinner(state, winner) {
            state.winner = winner
        }
    },
    actions: {
        getGameId({ commit }) {
            let url = "api/getgame"
            console.log(url)
            Axios.get(url).then((response) => {
                commit('setGame', response.data.response)
            })
        },
        newGame({ commit },curr) {
            let url = "api/newgame?current=" + curr
            console.log(url)
            Axios.post(url).then((response) => {
                this.dispatch('getGameId', response.data.response)
            })
        },
        getList({ commit }) {
            let url = "api/getlist?gameid=" + this.state.gameid
            console.log(url)
            Axios.get(url).then((response) => {
                commit('setList', response.data.response.list)
                commit('setWinner', response.data.response.winner)
            })
        },
        sendGuess({ commit }, guess) {
            let url = "api/sendguess"
            Axios.post(url, guess).then((response) => {
            })
        }
    }
})

